import { Editor, EditorMode } from "./editorObjects/Editor";
import { build as buildPoint } from "./editorObjects/Point";
import { composeReducers } from "immer-reducer";
import { createActionCreators, createReducerFunction } from "immer-reducer";
import { DraggableOperationsImmerReducer } from "./immerReducers/DraggableOperationsImmerReducer";
import { PersistImmerReducer } from "./immerReducers/PersistImmerReducer";
import { ChangeEditorModeImmerReducer } from "./immerReducers/ChangeEditorModeImmerReducer";
import { MainImmerReducer } from "./immerReducers/MainImmerReducer";
import { MouseSelectionImmerReducer } from "./immerReducers/MouseSelectionImmerReducer";

const defaultAppData: Editor = {
  appData: {
    beads: {},
    lines: {},
  },
  ui: {
    lineSettings: {
      autoFill: true,
    },
    saveInfo: {
      saved: false,
    },
    selectedLinesIds: {},
    selectedLinesIdsCount: 0,
    selectedBeadsIds: {},
    selectedBeadsIdsCount: 0,
    mouseSelection: {
      start: buildPoint(0, 0),
      end: buildPoint(0, 0),
      isDragStarted: false,
      isDragged: false,
    },
  },
  mode: EditorMode.Idle,
};

export const Actions = {
  Main: createActionCreators(MainImmerReducer),
  Draggable: createActionCreators(DraggableOperationsImmerReducer),
  Persist: createActionCreators(PersistImmerReducer),
  ChangeEditorMode: createActionCreators(ChangeEditorModeImmerReducer),
  MouseSelection: createActionCreators(MouseSelectionImmerReducer),
};

export const store = composeReducers(
  createReducerFunction(MainImmerReducer, defaultAppData),
  createReducerFunction(DraggableOperationsImmerReducer, defaultAppData),
  createReducerFunction(PersistImmerReducer, defaultAppData),
  createReducerFunction(ChangeEditorModeImmerReducer, defaultAppData),
  createReducerFunction(MouseSelectionImmerReducer, defaultAppData)
);
