import { Editor } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { build as buildPoint } from "../editorObjects/Point";
import { contain } from "../editorObjects/Point";
import { difference } from "lodash";

export class MouseSelectionImmerReducer extends ImmerReducer<Editor> {
  action_onEditBeadsMouseDown(x, y) {
    this.draftState.ui.mouseSelection.isDragStarted = true;

    this.draftState.ui.mouseSelection.start.x = x;
    this.draftState.ui.mouseSelection.start.y = y;
    this.draftState.ui.mouseSelection.end.x = x;
    this.draftState.ui.mouseSelection.end.y = y;
  }

  action_onEditBeadsMouseMove(x, y) {
    this.draftState.ui.mouseSelection.end.x = x;
    this.draftState.ui.mouseSelection.end.y = y;

    if (this.draftState.ui.mouseSelection.isDragged === false) {
      const { start, end } = this.draftState.ui.mouseSelection;
      const deltaX = end.x - start.x;
      const deltaY = end.y - start.y;

      if (Math.abs(deltaX) > 5 || Math.abs(deltaY) > 5) {
        this.draftState.ui.mouseSelection.isDragged = true;
      }
    }

    if (this.draftState.ui.mouseSelection.isDragged === true) {
      const { start, end } = this.draftState.ui.mouseSelection;
      const p1 = buildPoint(Math.min(start.x, end.x), Math.min(start.y, end.y));
      const p2 = buildPoint(Math.max(start.x, end.x), Math.max(start.y, end.y));
      this.draftState.ui.selectedBeadsIds = {};
      this.draftState.ui.selectedBeadsIdsCount = 0;
      Object.keys(this.draftState.appData.beads).forEach(beadId => {
        const bead = this.draftState.appData.beads[beadId];
        const p3 = buildPoint(bead.position.x + 10, bead.position.y + 10);
        if (contain(p1, p2, p3)) {
          this.draftState.ui.selectedBeadsIds[beadId] = true;
          this.draftState.ui.selectedBeadsIdsCount =
            this.draftState.ui.selectedBeadsIdsCount + 1;
        }
      });
    }
  }

  action_onEditBeadsMouseUp() {
    if (this.draftState.ui.mouseSelection.isDragged === false) {
      if (this.draftState.ui.selectedBeadsIdsCount > 0) {
        this.draftState.ui.selectedBeadsIds = {};
      }
    }

    this.draftState.ui.mouseSelection.isDragStarted = false;
    this.draftState.ui.mouseSelection.isDragged = false;
    this.draftState.ui.mouseSelection.start.x = 0;
    this.draftState.ui.mouseSelection.start.y = 0;
    this.draftState.ui.mouseSelection.end.x = 0;
    this.draftState.ui.mouseSelection.end.y = 0;
  }

  action_onEditLinesMouseDown(x: number, y: number) {
    this.draftState.ui.mouseSelection.isDragStarted = true;
    this.draftState.ui.mouseSelection.start.x = x;
    this.draftState.ui.mouseSelection.start.y = y;
    this.draftState.ui.mouseSelection.end.x = x;
    this.draftState.ui.mouseSelection.end.y = y;
  }

  action_onEditLinesMouseMove(x: number, y: number) {
    this.draftState.ui.mouseSelection.end.x = x;
    this.draftState.ui.mouseSelection.end.y = y;

    if (this.draftState.ui.mouseSelection.isDragged === false) {
      const { start, end } = this.draftState.ui.mouseSelection;
      const deltaX = end.x - start.x;
      const deltaY = end.y - start.y;
      if (Math.abs(deltaX) > 5 || Math.abs(deltaY) > 5) {
        this.draftState.ui.mouseSelection.isDragged = true;
      }
    }

    if (this.draftState.ui.mouseSelection.isDragged === true) {
      const { start, end } = this.draftState.ui.mouseSelection;
      const p1 = buildPoint(Math.min(start.x, end.x), Math.min(start.y, end.y));
      const p2 = buildPoint(Math.max(start.x, end.x), Math.max(start.y, end.y));

      const selectedIds = Object.keys(this.draftState.ui.selectedLinesIds);
      const newIds: string[] = [];

      Object.keys(this.draftState.appData.lines).forEach(lineId => {
        const line = this.draftState.appData.lines[lineId];
        if (contain(p1, p2, line.start) || contain(p1, p2, line.end)) {
          newIds.push(lineId);
        }
      });

      const diff = difference(newIds, selectedIds);
      
      if (diff.length > 0) {
        this.draftState.ui.selectedLinesIds = {};
        this.draftState.ui.selectedLinesIdsCount = 0;
        newIds.forEach(id => {
          this.draftState.ui.selectedLinesIds[id] = true;
          this.draftState.ui.selectedLinesIdsCount =
            this.draftState.ui.selectedLinesIdsCount + 1;
        });
      }
      // this.draftState.ui.selectedLinesIds = {};
      // this.draftState.ui.selectedLinesIdsCount = 0;
      // Object.keys(this.draftState.appData.lines).forEach(lineId => {
      //   const line = this.draftState.appData.lines[lineId];
      //   if (contain(p1, p2, line.start) || contain(p1, p2, line.end)) {
      //     this.draftState.ui.selectedLinesIds[lineId] = true;
      //     this.draftState.ui.selectedLinesIdsCount =
      //       this.draftState.ui.selectedLinesIdsCount + 1;
      //   }
      // });
    }
  }

  action_onEditLinesMouseUp() {
    if (this.draftState.ui.mouseSelection.isDragged === false) {
      if (this.draftState.ui.selectedLinesIdsCount > 0) {
        this.draftState.ui.selectedLinesIds = {};
      }
    }

    this.draftState.ui.mouseSelection.isDragStarted = false;
    this.draftState.ui.mouseSelection.isDragged = false;
    this.draftState.ui.mouseSelection.start.x = 0;
    this.draftState.ui.mouseSelection.start.y = 0;
    this.draftState.ui.mouseSelection.end.x = 0;
    this.draftState.ui.mouseSelection.end.y = 0;
  }
}
