import { immerable } from "immer";

export interface Point {
  x: number;
  y: number;
  [immerable]: boolean;
}

export interface Bead {
  id: string;
  position: Point;
  [immerable]: boolean;
}

export interface Line {
  id: string;
  start: Point;
  end: Point;
  beadIds: string[];
  [immerable]: boolean;
  settings: {
    beadCount: number;
    autoFill: boolean;
  };
}

export interface AppData {
  beads: { [s: string]: Bead };
  lines: { [s: string]: Line };
}

export enum EditorMode {
  Idle = "Move",
  EditLines = "Edit",
  AddLine = "Add Line",
  EditBeads = "EditBeads",
}

export interface EditorUi {
  lineSettings: {
    autoFill: boolean;
  };
  saveInfo: {
    saved: boolean;
  };
  selectedLinesIds: { [s: string]: boolean };
  selectedLinesIdsCount: number;
  selectedBeadsIds: { [s: string]: boolean };
  selectedBeadsIdsCount: number;
  mouseSelection: {
    start: Point;
    end: Point;
    isDragStarted: boolean;
    isDragged: boolean;
  };
}

export interface Editor {
  appData: AppData;
  ui: EditorUi;
  mode: EditorMode;
}
