import React, { useState, MouseEvent, useRef } from "react";
import { StraightLine } from "./StraightLine";
import { getTranslate3dCss } from "../operations/MapValuesToCss";
import { useDispatch } from "react-redux";
import { Point } from "../../editorObjects/Editor";
import { Actions } from "../../store";

export const AddLineMode = ({ isActive }) => {
  if (isActive === false) {
    return <React.Fragment />;
  }

  return <AddLineModeContent />;
};

const AddLineModeContent = () => {
  interface SimplePoint {
    x: number;
    y: number;
  }
  const [startPoint, setStartPoint] = useState<SimplePoint>({
    x: 0,
    y: 0,
  });
  const [endPoint, setEndPoint] = useState<SimplePoint>({
    x: 0,
    y: 0,
  });
  const [isDragEnabled, setIsDragEnabled] = useState<boolean>(false);
  const lineRef = useRef<HTMLDivElement>(null);
  const onMouseDown = (event: MouseEvent<HTMLElement>) => {
    if (lineRef.current) {
      const rect = lineRef.current.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;
      setStartPoint({ x, y });
      setEndPoint({ x, y });
      setIsDragEnabled(true);
    }
  };
  const onMouseUp = (event: MouseEvent<HTMLElement>) => {
    if (isDragEnabled) {
      if (lineRef.current) {
        const rect = lineRef.current.getBoundingClientRect();
        const x = event.clientX - rect.left;
        const y = event.clientY - rect.top;
        setEndPoint({ x, y });
        onAddLine(startPoint, endPoint);
        setIsDragEnabled(false);
      }
    }
  };
  const onMouseMove = (event: MouseEvent<HTMLElement>) => {
    if (isDragEnabled) {
      if (lineRef.current) {
        const rect = lineRef.current.getBoundingClientRect();
        const x = event.clientX - rect.left;
        const y = event.clientY - rect.top;
        setEndPoint({ x, y });
      }
    }
  };
  const dispatch = useDispatch();

  const onAddLine = (start: SimplePoint, end: SimplePoint) => {
    dispatch(Actions.Draggable.action_onAddLine(start as Point, end as Point));
  };

  return (
    <div>
      <div
        onMouseDown={onMouseDown}
        onMouseUp={onMouseUp}
        onMouseMove={onMouseMove}
        onMouseLeave={onMouseUp}
        className="add-line-wrapper"
        ref={lineRef}
      ></div>

      {isDragEnabled ? (
        <div>
          <div
            style={{
              transform: getTranslate3dCss(startPoint.x, startPoint.y),
              width: 10,
              height: 10,
            }}
            className={"line-start"}
          >
            &nbsp;
          </div>
          <div
            style={{
              transform: getTranslate3dCss(endPoint.x, endPoint.y),
              width: 10,
              height: 10,
            }}
            className={"line-end"}
          >
            &nbsp;
          </div>
          <StraightLine start={startPoint} end={endPoint} />
        </div>
      ) : null}
    </div>
  );
};
