import React from "react";
import { Bead } from "../../editorObjects/Editor";
import { getTranslate3dCss } from "../operations/MapValuesToCss";
import { Actions } from "../../store";
import { useDispatch } from "react-redux";

export const DisplayBead = (props: {
  bead: Bead;
  opacity?: number;
  zIndex?: number;
  isSelected: boolean;
}) => {
  const { bead, opacity, zIndex, isSelected } = props;
  const isOpacity = opacity !== undefined && opacity >= 0 && opacity <= 1;
  const isZindex = zIndex !== undefined && zIndex >= 0 && zIndex <= 9999;

  const dispatch = useDispatch();
  const onClick = () => {
    dispatch(Actions.Draggable.action_onClickBead(bead.id));
  };

  return (
    <div
      className="bead-item"
      onClick={onClick}
      style={{
        transform: getTranslate3dCss(bead.position.x, bead.position.y),
        opacity: isOpacity ? opacity : 1,
        zIndex: isZindex ? zIndex : undefined,
        border: isSelected ? "2px solid #000" : "",
      }}
    >
      &nbsp;
    </div>
  );
};
