import React, { useRef, MouseEvent } from "react";
import { getTranslate3dCss } from "./MapValuesToCss";
import { Editor } from "../../editorObjects/Editor";
import { useSelector } from "react-redux";

export const MouseSelection = (props: {
  isVisible: boolean;
  onMouseDown: any;
  onMouseUp: any;
  onMouseMove: any;
}) => {
  const { isVisible } = props;
  const sceneWrapperRef = useRef<HTMLDivElement>(null);

  const start = useSelector((state: Editor) => {
    return state.ui.mouseSelection.start;
  });

  const end = useSelector((state: Editor) => {
    return state.ui.mouseSelection.end;
  });

  const mouseSelectionIsDragStarted = useSelector((state: Editor) => {
    return state.ui.mouseSelection.isDragStarted;
  });

  if (isVisible === false) {
    return <React.Fragment />;
  }

  const isActive = mouseSelectionIsDragStarted === true;
  const { onMouseDown, onMouseUp, onMouseMove } = props;

  const onMouseDownHandler = (event: MouseEvent<HTMLElement>) => {
    if (sceneWrapperRef.current) {
      const rect = sceneWrapperRef.current.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;
      onMouseDown(x, y);
    }
  };

  const onMouseUpHandler = (event: MouseEvent<HTMLElement>) => {
    onMouseUp();
  };

  const onMouseMoveHandler = (event: MouseEvent<HTMLElement>) => {
    if (isActive) {
      if (sceneWrapperRef.current) {
        const rect = sceneWrapperRef.current.getBoundingClientRect();
        const x = event.clientX - rect.left;
        const y = event.clientY - rect.top;
        onMouseMove(x, y);
      }
    }
  };

  const width = Math.abs(end.x - start.x);
  const height = Math.abs(end.y - start.y);
  const top = Math.min(end.x, start.x);
  const left = Math.min(end.y, start.y);

  // @TODO: czy chce odznaczać zaznaczenie po wyjściu poza teren edytora?

  return (
    <div
      className={"mouse-selection"}
      ref={sceneWrapperRef}
      onMouseDown={onMouseDownHandler}
      onMouseUp={onMouseUpHandler}
      // onMouseLeave={onMouseUpHandler}
      onMouseMove={onMouseMoveHandler}
      style={{
        zIndex: isActive ? 23 : 9,
      }}
    >
      {isActive ? (
        <div
          style={{
            width,
            height,
            backgroundColor: "rgba(0,0,0, 0.05)",
            border: "1px solid rgba(0,0,0,.3)",
            transform: getTranslate3dCss(top, left),
          }}
        ></div>
      ) : null}
    </div>
  );
};
