import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faEdit, faArrowsAlt } from "@fortawesome/free-solid-svg-icons";
import { EditorMode } from "../../editorObjects/Editor";
import { uiButton } from "../colors";
import { useDispatch } from "react-redux";
import { Actions } from "../../store";

export const EditorModes = ({ editorMode }) => {
  const dispatch = useDispatch();

  const onChangeEditorMode = (newEditorMode: EditorMode) => {
    dispatch(Actions.ChangeEditorMode.action_onChangeEditorMode(newEditorMode));
  };

  return (
    <div className="">
      <div
        className={`
              button 
              ${editorMode === EditorMode.Idle ? "is-active" : ""}
            `}
        onClick={() => onChangeEditorMode(EditorMode.Idle)}
      >
        <span className="icon is-small">
          <FontAwesomeIcon icon={faArrowsAlt} color={uiButton} />
        </span>
        <span>Idle</span>
      </div>

      <div
        className={`
              button 
              ${editorMode === EditorMode.EditLines ? "is-active" : ""}
            `}
        onClick={() => onChangeEditorMode(EditorMode.EditLines)}
      >
        <span className="icon is-small">
          <FontAwesomeIcon icon={faEdit} color={uiButton} />
        </span>
        <span>EditLines</span>
      </div>

      <div
        className={`
              button 
              ${editorMode === EditorMode.EditBeads ? "is-active" : ""}
            `}
        onClick={() => onChangeEditorMode(EditorMode.EditBeads)}
      >
        <span className="icon is-small">
          <FontAwesomeIcon icon={faEdit} color={uiButton} />
        </span>
        <span>Edit Beads</span>
      </div>

      <div
        className={`
              button 
              ${editorMode === EditorMode.AddLine ? "is-active" : ""}
            `}
        onClick={() => onChangeEditorMode(EditorMode.AddLine)}
      >
        <span className="icon is-small">
          <FontAwesomeIcon icon={faPlus} color={uiButton} />
        </span>
        <span>AddLine</span>
      </div>
    </div>
  );
};
