import React from "react";
import { useSelector } from "react-redux";
import { Editor, EditorMode } from "../../editorObjects/Editor";
import { MemoizedDisplayLinesGroup } from "./DisplayLinesGroup";
import { MemoizedDisplayBeadsGroup } from "./DisplayBeadsGroup";

export const GroupSelectionPlaceholder = ({ mode }) => {
  const selectedBeadsIdsCount = useSelector((state: Editor) => {
    return state.ui.selectedBeadsIdsCount;
  });
  
  const selectedLinesIdsCount = useSelector((state: Editor) => {
    return state.ui.selectedLinesIdsCount;
  });
  
  if (mode === EditorMode.EditBeads && selectedBeadsIdsCount >= 2) {
    return (
      <MemoizedDisplayBeadsGroup
        selectedBeadsIdsCount={selectedBeadsIdsCount}
      />
    );
  }

  if (mode === EditorMode.EditLines && selectedLinesIdsCount >= 2) {
    return (
      <MemoizedDisplayLinesGroup
        selectedLinesIdsCount={selectedLinesIdsCount}
      />
    );
  }
  return <React.Fragment />;
};
