import { immerable } from "immer";
import { Line, Point } from "./Editor";
import { generate } from "shortid";
import { normalize, scale, add, build as buildPoint } from "./Point";

export const build = (props: {
  start: Point;
  end: Point;
  beadIds?: string[];
}): Line => {
  const { start, end, beadIds } = props;
  const id = generate();
  return {
    id,
    start,
    end,
    beadIds: beadIds && beadIds.length > 0 ? beadIds : [],
    settings: {
      beadCount: 0,
      autoFill: true,
    },
    [immerable]: true,
  } as Line;
};

export const getLength = (line: Line): number => {
  return Math.sqrt(
    (line.end.x - line.start.x) * (line.end.x - line.start.x) +
      (line.end.y - line.start.y) * (line.end.y - line.start.y)
  );
};

export const getPointFromStart = (line: Line, distance: number): Point => {
  const x = line.end.x - line.start.x;
  const y = line.end.y - line.start.y;
  let point = buildPoint(x, y);
  point = normalize(point);
  point = scale(point, distance);
  point = add(point, line.start.x, line.start.y);
  return point;
};

export const getPointFromStartLine = (
  startX: number,
  startY: number,
  endX: number,
  endY: number,
  distance: number
): Point => {
  const x = endX - startX;
  const y = endY - startY;
  let point = buildPoint(x, y);
  point = normalize(point);
  point = scale(point, distance);
  point = add(point, startX, startY);
  return point;
};
