import React from "react";
import { EditorMode, Editor } from "../../editorObjects/Editor";
import { DraggableLines } from "./DraggableLines";
import { DisplaySimpleLine } from "./DisplaySimpleLine";
import { useSelector } from "react-redux";

export const DisplayLines = ({ mode }) => {
  const isEditable = mode === EditorMode.EditLines;
  const lines = useSelector((state: Editor) => {
    return Object.values(state.appData.lines);
  });
  const selectedLinesIds = useSelector((state: Editor) => {
    return state.ui.selectedLinesIds;
  });

  if (isEditable) {
    return <DraggableLines lines={lines} selectedLinesIds={selectedLinesIds} />;
  }

  return (
    <React.Fragment>
      {lines.map(line => {
        return <DisplaySimpleLine key={line.id} line={line} />;
      })}
    </React.Fragment>
  );
};
