import React from "react";
import { useSelector } from "react-redux";
import { Editor } from "../../editorObjects/Editor";
import { DisplayGroupBeads } from "./DisplayGroupBeads";

const DisplayBeadsGroup = ({ selectedBeadsIdsCount }) => {
  const selectedBead = useSelector((state: Editor) => {
    return Object.keys(state.ui.selectedBeadsIds).map(beadId => {
      return state.appData.beads[beadId];
    });
  });
  let xMin: number = Number.MAX_SAFE_INTEGER;
  let xMax: number = Number.MAX_SAFE_INTEGER * -1;
  let yMin: number = Number.MAX_SAFE_INTEGER;
  let yMax: number = Number.MAX_SAFE_INTEGER * -1;
  selectedBead.forEach(bead => {
    const startX = bead.position.x;
    const endX = bead.position.x + 10;
    const startY = bead.position.y;
    const endY = bead.position.y + 10;
    xMin = Math.min(xMin, startX, endX);
    xMax = Math.max(xMax, startX, endX);
    yMin = Math.min(yMin, startY, endY);
    yMax = Math.max(yMax, startY, endY);
  });
  const location = {
    x: xMin,
    y: yMin,
    w: xMax - xMin + 10,
    h: yMax - yMin + 10,
  };
  return <DisplayGroupBeads location={location} />;
};

export const MemoizedDisplayBeadsGroup = React.memo(DisplayBeadsGroup);
