import React from "react";
import { useDispatch } from "react-redux";
import { Actions } from "../../store";
import { MouseSelection } from "../operations/MouseSelection";

export const MouseSelectionLines = ({ isVisible }) => {
  const dispatch = useDispatch();

  const onMouseDown = (x, y) => {
    dispatch(Actions.MouseSelection.action_onEditLinesMouseDown(x, y));
  };

  const onMouseMove = (x, y) => {
    dispatch(Actions.MouseSelection.action_onEditLinesMouseMove(x, y));
  };

  const onMouseUp = () => {
    dispatch(Actions.MouseSelection.action_onEditLinesMouseUp());
  };

  return (
    <MouseSelection
      isVisible={isVisible}
      onMouseDown={onMouseDown}
      onMouseMove={onMouseMove}
      onMouseUp={onMouseUp}
    />
  );
};
