import React from "react";
import { getTranslate3dCss } from "./MapValuesToCss";

export const DisplayGroupBeads = ({ location }) => {
  const { x, y, w, h } = location;
  return (
    <div
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        width: w,
        height: h,
        transform: getTranslate3dCss(x, y),
        opacity: 1,
        zIndex: 22,
        border: "1px dashed rgba(0,0,0,0.2)",
        background: "rgba(0,0,0,0.05)",
      }}
    >
      &nbsp;
    </div>
  );
};
