import React from "react";
import { getTranslate3dCss } from "./MapValuesToCss";
import { DndProvider } from "react-dnd";
import HTML5Backend from "react-dnd-html5-backend";
import { DraggableCore } from "react-draggable";

export const DisplayGroupLines = ({ location, onDrag }) => {
  const { x, y, w, h } = location;
  return (
    <DndProvider backend={HTML5Backend}>
      <DraggableCore
        onStart={onDrag}
        onDrag={onDrag}
        onStop={onDrag}
        scale={1}
        disabled={false}
      >
        <div
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: w,
            height: h,
            transform: getTranslate3dCss(x, y),
            opacity: 1,
            zIndex: 22,
            border: "1px dashed rgba(0,0,0,0.2)",
            background: "rgba(0,0,0,0.05)",
            cursor: "move",
          }}
        >
          &nbsp;
        </div>
      </DraggableCore>
    </DndProvider>
  );
};
