import React from "react";
import { DisplayBead } from "./DisplayBead";
import { useSelector } from "react-redux";
import { Editor, EditorMode } from "../../editorObjects/Editor";

export const DisplayBeads = ({ mode }) => {
  const fadeOut = mode === EditorMode.EditLines;
  const onTop = mode === EditorMode.EditBeads;
  const beads = useSelector((state: Editor) => {
    return Object.values(state.appData.beads);
  });
  const selectedBeadsIds = useSelector((state: Editor) => {
    return state.ui.selectedBeadsIds;
  });

  return (
    <React.Fragment>
      {beads.map(bead => {
        return (
          <DisplayBead
            key={bead.id}
            bead={bead}
            opacity={fadeOut ? 0.5 : 1}
            isSelected={selectedBeadsIds[bead.id] !== undefined}
            zIndex={fadeOut ? 10 : onTop ? 20 : undefined}
          />
        );
      })}
    </React.Fragment>
  );
};
