import { Point } from "./Editor";
import { immerable } from "immer";

export const build = (x: number = 0, y: number = 0): Point => {
  return {
    x,
    y,
    [immerable]: true,
  } as Point;
};

export const getLength = (point: Point) => {
  return Math.sqrt(point.x * point.x + point.y * point.y);
};

export const normalize = (point: Point) => {
  const length = getLength(point);
  const useLen = length === 0;
  const x = useLen ? 0 : point.x / length;
  const y = useLen ? 0 : point.y / length;
  return build(x, y);
};

export const scale = (point: Point, scalar: number) => {
  return build(point.x * scalar, point.y * scalar);
};

export const add = (point: Point, x: number, y: number) => {
  return build(point.x + x, point.y + y);
};

export const getDistance = (x1, y1, x2, y2): number => {
  return Math.sqrt((x2 - x1) * (x2 - x1) + (y2 - y1) * (y2 - y1));
};

export const getDistanceBetweenPoints = (start: Point, end: Point): number => {
  return Math.sqrt(
    (end.x - start.x) * (end.x - start.x) +
      (end.y - start.y) * (end.y - start.y)
  );
};

export const getRotate = (y, x) => {
  return Math.atan2(y, x);
};

export const contain = (p1: Point, p2: Point, p: Point): boolean => {
  return p.x > p1.x && p.x < p2.x && p.y > p1.y && p.y < p2.y;
};
