import React from "react";
import { Line } from "../../editorObjects/Editor";
import { StraightLine } from "./StraightLine";
import { DraggableData, DraggableCore } from "react-draggable";
import { SelectionStraightLine } from "./SelectionStraightLine";
import { SelectedStraightLine } from "./SelectedStraightLine";
import { getTranslate3dCss } from "../operations/MapValuesToCss";
import { useDispatch } from "react-redux";
import { Actions } from "../../store";

export const DisplayLine = (props: { line: Line; isSelected: boolean }) => {
  const { isSelected, line } = props;
  const dispatch = useDispatch();
  const eventHandlerStartPoint = (_, { x, y }: DraggableData) => {
    dispatch(Actions.Draggable.action_onStartPointDrag(line.id, { x, y }));
  };
  const eventHandlerEndPoint = (_, { x, y }: DraggableData) => {
    dispatch(Actions.Draggable.action_onEndPointDrag(line.id, { x, y }));
  };
  const onClick = () => {
    dispatch(Actions.Draggable.action_onClickLine(line.id));
  };
  const itemWidth = 10;
  const itemHeight = 10;

  return (
    <div>
      <DraggableCore
        onStart={eventHandlerStartPoint}
        onDrag={eventHandlerStartPoint}
        onStop={eventHandlerStartPoint}
        scale={1}
        disabled={false}
      >
        <div
          style={{
            transform: getTranslate3dCss(line.start.x, line.start.y),
            zIndex: isSelected === true ? 22 : 20,
            width: itemWidth,
            height: itemHeight,
          }}
          className={"line-start"}
        >
          &nbsp;
        </div>
      </DraggableCore>

      <DraggableCore
        onStart={eventHandlerEndPoint}
        onDrag={eventHandlerEndPoint}
        onStop={eventHandlerEndPoint}
        disabled={false}
        scale={1}
      >
        <div
          style={{
            transform: getTranslate3dCss(line.end.x, line.end.y),
            zIndex: isSelected === true ? 22 : 20,
            width: itemWidth,
            height: itemHeight,
          }}
          className={"line-end"}
        >
          &nbsp;
        </div>
      </DraggableCore>

      {isSelected === true ? (
        <SelectedStraightLine
          start={line.start}
          end={line.end}
          lineId={line.id}
        />
      ) : (
        <SelectionStraightLine
          start={line.start}
          end={line.end}
          onClick={onClick}
        />
      )}

      <StraightLine start={line.start} end={line.end} />
    </div>
  );
};
