import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor } from "../../editorObjects/Editor";
import { Actions } from "../../store";
import { DraggableData } from "react-draggable";
import { DisplayGroupLines } from "./DisplayGroupLines";

export const DisplayLinesGroup = ({ selectedLinesIdsCount }) => {
  const selectedLines = useSelector((state: Editor) => {
    return Object.keys(state.ui.selectedLinesIds).map(lineId => {
      return state.appData.lines[lineId];
    });
  });
  let xMin: number = Number.MAX_SAFE_INTEGER;
  let xMax: number = Number.MAX_SAFE_INTEGER * -1;
  let yMin: number = Number.MAX_SAFE_INTEGER;
  let yMax: number = Number.MAX_SAFE_INTEGER * -1;
  selectedLines.forEach(line => {
    const start = line.start;
    const end = line.end;
    xMin = Math.min(xMin, start.x, end.x);
    xMax = Math.max(xMax, start.x, end.x);
    yMin = Math.min(yMin, start.y, end.y);
    yMax = Math.max(yMax, start.y, end.y);
  });
  const location = {
    x: xMin,
    y: yMin,
    w: xMax - xMin + 10,
    h: yMax - yMin + 10,
  };
  const dispatch = useDispatch();
  const eventDragHandler = (_, { deltaX, deltaY }: DraggableData) => {
    dispatch(Actions.Draggable.action_onGroupLineDrag({ deltaX, deltaY }));
  };
  return <DisplayGroupLines location={location} onDrag={eventDragHandler} />;
};

export const MemoizedDisplayLinesGroup = React.memo(DisplayLinesGroup);
