import { Editor, EditorMode, Point } from "../editorObjects/Editor";
import {
  build as buildPoint,
  getDistanceBetweenPoints,
} from "../editorObjects/Point";
import { ImmerReducer } from "immer-reducer";
import { build as buildBead } from "../editorObjects/Bead";
import { build as buildLine } from "../editorObjects/Line";
import { getPointFromStartLine } from "../editorObjects/Line";

const boxOffset = 5;

export class DraggableOperationsImmerReducer extends ImmerReducer<Editor> {
  action_onAddLine(start: Point, end: Point) {
    const useAutoFill = this.draftState.ui.lineSettings.autoFill;
    const beadIds = [];
    const line = buildLine({ start, end, beadIds });
    line.settings.autoFill = useAutoFill;
    this.draftState.appData.lines[line.id] = line;
    this.draftState.mode = EditorMode.EditLines;
    this.updateBeadsPositions(line.id);
    this.draftState.ui.saveInfo.saved = false;
  }

  action_onStartPointDrag(
    key: string,
    data: {
      x: number;
      y: number;
    }
  ) {
    if (this.draftState.appData.lines[key]) {
      const line = this.draftState.appData.lines[key];
      line.start.x = data.x - boxOffset;
      line.start.y = data.y - boxOffset;
      this.updateBeadsPositions(line.id);
      this.draftState.ui.saveInfo.saved = false;
    }
  }

  action_onEndPointDrag(
    key: string,
    data: {
      x: number;
      y: number;
    }
  ) {
    if (this.draftState.appData.lines[key]) {
      const line = this.draftState.appData.lines[key];
      line.end.x = data.x - boxOffset;
      line.end.y = data.y - boxOffset;
      this.updateBeadsPositions(line.id);
      this.draftState.ui.saveInfo.saved = false;
    }
  }

  action_onLineDrag(
    key: string,
    data: {
      deltaX: number;
      deltaY: number;
    }
  ) {
    if (this.draftState.appData.lines[key]) {
      const line = this.draftState.appData.lines[key];
      line.start.x = line.start.x + data.deltaX;
      line.start.y = line.start.y + data.deltaY;
      line.end.x = line.end.x + data.deltaX;
      line.end.y = line.end.y + data.deltaY;
      this.updateBeadsPositions(line.id);
      this.draftState.ui.saveInfo.saved = false;
    }
  }

  action_onGroupLineDrag(data: { deltaX: number; deltaY: number }) {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach(lineId => {
      if (this.draftState.appData.lines[lineId]) {
        const line = this.draftState.appData.lines[lineId];
        line.start.x = line.start.x + data.deltaX;
        line.start.y = line.start.y + data.deltaY;
        line.end.x = line.end.x + data.deltaX;
        line.end.y = line.end.y + data.deltaY;
        this.updateBeadsPositions(line.id);
        this.draftState.ui.saveInfo.saved = false;
      }
    });
  }

  action_onGroupBeadsDrag(data: { deltaX: number; deltaY: number }) {}

  action_onClickLine(lineId: string) {
    this.draftState.ui.selectedLinesIds = {};
    this.draftState.ui.selectedLinesIds[lineId] = true;
    this.draftState.ui.selectedLinesIdsCount = 1;
  }

  action_onClickBead(beadId: string) {
    this.draftState.ui.selectedBeadsIds = {};
    this.draftState.ui.selectedBeadsIds[beadId] = true;
    this.draftState.ui.selectedBeadsIdsCount = 1;
  }

  private updateBeadsPositions(lineId: string) {
    const startOffset = 25;
    const offset = 5;
    const beadWidth = 20;
    const size = beadWidth + offset;
    const currentLine = this.draftState.appData.lines[lineId];
    const { start, end, beadIds } = currentLine;
    const len = getDistanceBetweenPoints(start, end);
    const count = Math.floor((len - startOffset - offset) / size);
    const currentCount = beadIds.length;
    if (count > currentCount) {
      const remainingCount = count - currentCount;
      for (let i = 0; i < remainingCount; i++) {
        const position = buildPoint();
        const bead = buildBead(position);
        this.draftState.appData.beads[bead.id] = bead;
        this.draftState.appData.lines[lineId].beadIds.push(bead.id);
      }
    } else if (count < currentCount) {
      const remainingCount = currentCount - count;
      for (let i = 0; i < remainingCount; i++) {
        const arrLen = this.draftState.appData.lines[lineId].beadIds.length;
        const lastItemBeadId = this.draftState.appData.lines[
          lineId
        ].beadIds.splice(arrLen - 1, 1);
        delete this.draftState.appData.beads[lastItemBeadId[0]];
      }
    }
    let currentDistance = startOffset;
    this.draftState.appData.lines[lineId].beadIds.forEach(beadId => {
      const newPosition = getPointFromStartLine(
        start.x,
        start.y,
        end.x,
        end.y,
        currentDistance
      );
      const halfSize = beadWidth / 2;
      const x = newPosition.x - halfSize + 5;
      const y = newPosition.y - halfSize + 5;
      this.draftState.appData.beads[beadId].position.x = x;
      this.draftState.appData.beads[beadId].position.y = y;
      currentDistance = currentDistance + size;
    });
  }
}
